@import url(https://fonts.googleapis.com/css2?family=Noto+Sans+KR:wght@400;500;700&display=swap);
.movies_main {
  position: relative;
  width: 100vw;
  height: 700px;
  background-size: cover;
  background-repeat: no-repeat;
}

.movies_main .main_gradient {
  width: 100%;
  height: 100px;
  position: absolute;
  bottom: 0;
  background-image: linear-gradient(to bottom, rgba(255, 255, 255, 0), rgba(255, 255, 255, 0.5));
}

.movies_main .main_poster {
  position: absolute;
  bottom: 50px;
  left: 5%;
  max-width: 250px;
  border-radius: 5px;
  box-shadow: -2px 6px 19px 0px #7f818e;
  cursor: pointer;
}

.movies_main .main_title {
  position: absolute;
  top: 180px;
  right: 4%;
  color: #fff;
  font-size: 47px;
}

.movies_main .main_summary {
  position: absolute;
  width: 50%;
  bottom: 50px;
  right: 4%;
  color: #fff;
  font-size: 16px;
  text-align: right;
  white-space: normal;
  display: -webkit-box;
  -webkit-line-clamp: 4;
  -webkit-box-orient: vertical;
  overflow: hidden;

}

@media only screen and (max-width: 720px) {
  .movies_main .main_poster {
    display: none;
  }

  .movies_main .main_summary {
    width: 70%;
  }
}
.movie {
  display: flex;
  grid-gap: 50px 20px;
  gap: 50px 20px;
  justify-content: center;
  flex-wrap: wrap;
  max-width: 1920px;
  margin: 0 1.5%;
}

.movies_movie {
  position: relative;
  display: flex;
  flex-basis: 49%;
  min-width: 625px;
  max-width: 625px;
  height: 280px;
  padding: 20px;
  background: #fff;
  border-radius: 8px;
  border: 1px solid rgba(70, 77, 82, 0.082);
  box-sizing: border-box;
  box-shadow: rgba(0, 0, 0, 0.024) -1px 1px 1px;
}

.movies_movie .movie_poster {
  position: absolute;
  top: -20px;
  left: 20px;
  width: 180px;
  border-radius: 5px;
  box-shadow: -2px 6px 19px 0px #7f818e;
  cursor: pointer;
}

.movies_movie .movie_poster:hover {
  top: -31px;
  transition: all 0.3s cubic-bezier(.25, .8, .25, 1);
}

.movies_movie .movie_heart {
  width: 28px;
  height: 28x;
  position: absolute;
  top: 0;
  left: 375px;
  cursor: pointer;
}

.movies_movie .movie_data {
  position: absolute;
  left: 200px;
  padding: 0 30px;
  overflow: hidden;
}

.movie_data .movie_title {
  margin: 0 0 12px 2px;
  font-size: 20px;
}

.movie_data .movie_genres {
  display: flex;
  flex-wrap: wrap;
  list-style: none;
  margin: 0;
  padding: 0;
  font-size: 12px;
}

.movie_data .movie_genres li {
  margin-right: 4px;
  margin-bottom: 4px;
  padding: 4px 8px;
  border: 1px solid rgba(70, 77, 82, 0.082);
  box-sizing: border-box;
  box-shadow: rgba(0, 0, 0, 0.024) -1px 1px 1px;
  border-radius: 20px;
  cursor: pointer;
}

.movie_data .movie_summary {
  margin-top: 60px;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  white-space: normal;
  display: -webkit-box;
  -webkit-line-clamp: 4;
  -webkit-box-orient: vertical;
  overflow: hidden;
  font-size: 14px;
  line-height: 1.7;
}

@media only screen and (max-width: 720px) {
  .movies_movie {
    min-width: 380px;
    height: 270px;
    padding: 0px;
    border: unset;
  }

  .movies_movie .movie_poster {
    position: absolute;
    top: 0;
    left: 0;
    width: 180px;
    height: 270px;
    border-radius: 5px 0 0 5px;
    box-shadow: unset;
    cursor: pointer;
  }

  .movies_movie .movie_data {
    left: 180px;
    padding: 12px;
  }

  .movies_movie .movie_heart {
    position: unset;
    margin-bottom: 10px;
  }

  .movie_data .movie_genres {
    display: none;
  }

  .movie_data .movie_summary {
    margin: 30px 0 0;
  }

}
:root {
  --loading-grey: #ededed;
}

@-webkit-keyframes loading {
  to {
    background-position-x: -20%;
  }
}

@keyframes loading {
  to {
    background-position-x: -20%;
  }
}

.skeleton {
  display: flex;
  grid-gap: 50px 20px;
  gap: 50px 20px;
  justify-content: center;
  flex-wrap: wrap;
  margin-bottom: 5%;
}

.skeleton_main {
  position: relative;
  width: 100vw;
  height: 700px;
  background-color: #fff;
}

.skeleton_main_poster {
  position: absolute;
  bottom: 50px;
  left: 5%;
  max-width: 250px;
  width: 250px;
  height: 375px;
  border-radius: 5px;
  box-shadow: -2px 6px 19px 0px #d5d6dd;
}

.skeleton_main_title {
  position: absolute;
  top: 230px;
  right: 4%;
  width: 360px;
  height: 60px;
  border-radius: 5px;
}

.skeleton_main_summary {
  position: absolute;
  bottom: 50px;
  right: 4%;
  width: 50%;
  height: 260px;
  border-radius: 5px;
}

.skeleton_movie {
  display: flex;
  grid-gap: 50px 20px;
  gap: 50px 20px;
  justify-content: center;
  flex-wrap: wrap;
  margin: 0 1.5%;
}

.skeleton_movies {
  position: relative;
  display: flex;
  flex-basis: 49%;
  min-width: 625px;
  max-width: 625px;
  height: 280px;
  padding: 20px;
  background: #fff;
  border-radius: 8px;
  border: 1px solid rgba(70, 77, 82, 0.082);
  box-sizing: border-box;
  box-shadow: rgba(0, 0, 0, 0.024) -1px 1px 1px;
}

.skeleton_movies_img {
  position: absolute;
  top: -20px;
  left: 20px;
  width: 180px;
  height: 270px;
  border-radius: 5px;
  box-shadow: -2px 6px 19px 0px #d5d6dd;
}

.skeleton_movies_data {
  position: absolute;
  left: 200px;
  padding: 0 30px;
}

.skeleton_movies_heart {
  width: 28px;
  height: 28x;
  position: absolute;
  top: 0;
  left: 375px;
  cursor: pointer;
}

.skeleton_movies_title {
  width: 300px;
  height: 29px;
  border-radius: 5px;
}

.skeleton_movies_genres {
  width: 290px;
  height: 27px;
  border-radius: 5px;
}

.skeleton_movies_summary {
  width: 360px;
  height: 85px;
  margin-top: 50px;
  border-radius: 5px;
}

.skeleton_main_poster,
.skeleton_main_title,
.skeleton_main_summary,
.skeleton_movies_img,
.skeleton_movies_title,
.skeleton_movies_genres,
.skeleton_movies_summary {
  background-color: #ededed;
  background-color: var(--loading-grey);
  background: linear-gradient(120deg,
      rgba(255, 255, 255, 0) 40%,
      rgba(255, 255, 255, .3) 50%,
      rgba(255, 255, 255, 0) 60%) #ededed;
  background: linear-gradient(120deg,
      rgba(255, 255, 255, 0) 40%,
      rgba(255, 255, 255, .3) 50%,
      rgba(255, 255, 255, 0) 60%) var(--loading-grey);
  background-size: 220% 100%;
  background-position-x: 120%;
  -webkit-animation: 1s loading ease-in-out infinite;
          animation: 1s loading ease-in-out infinite;
}

@media only screen and (max-width: 720px) {
  .skeleton_main_poster {
    display: none;
  }

  .skeleton_main_summary {
    width: 70%;
  }

  .skeleton_movies {
    min-width: 380px;
    height: 270px;
    padding: 0px;
    border: unset;
  }

  .skeleton_movies_img {
    position: absolute;
    top: 0;
    left: 0;
    width: 180px;
    height: 270px;
    border-radius: 5px 0 0 5px;
    box-shadow: unset;
    cursor: pointer;
  }

  .skeleton_movies_data {
    left: 180px;
    padding: 12px;
  }

  .skeleton_movies_heart {
    position: unset;
    margin-bottom: 10px;
  }

  .skeleton_movies_title {
    width: 174px;
  }

  .skeleton_movies_genres {
    display: none;
  }

  .skeleton_movies_summary {
    width: 176px;
    margin: 30px 0 0;
  }

}
